<template>
  <div id="app">
      <img src="./assets/logo.png" class="img">
      <Tabs type="card" @on-tab-remove="handleTabRemove" id="TabNav">
          <Tab-pane label="查询书籍" v-if="tab0">
            <search></search>
          </Tab-pane>
          <Tab-pane label="客服添加书籍" v-if="tab1">
            <addbook></addbook>
          </Tab-pane>
          <Tab-pane label="杂志上传"  v-if="tab2">
            <upload></upload>
          </Tab-pane>
          <Tab-pane label="订单查询" v-if="tab3">
              <order></order>
          </Tab-pane>
          <Tab-pane label="公众号分类" v-if="tab4">
              <wechat></wechat>
          </Tab-pane>
      </Tabs>
  </div>
</template>

<script>
import search from './components/search'
import addbook from './components/addbook'
import upload from './components/upload'
import order from './components/order'
import wechat from './components/wechat'

export default {
  data(){
    return{
      tab0: true,
      tab1: true,
      tab2: true,
      tab3: true,
      tab4: true,
    }
  },
  components:{
    search,
    addbook,
    upload,
    order,
    wechat
  },
  methods: {
      handleTabRemove (name) {
          this['tab' + name] = false;
      }
  }
}
</script>

<style>
body,html,ul{margin-top: 10px; padding: 0; list-style-type: none; text-decoration: none; color: black; font-family: "微软雅黑";}
#TabNav{ width:100%; margin-left: 1%;}
.img{ width: 5%; margin-left: 2%; margin-bottom: 2%;}
.btn1{ border-radius: 5px; background: white;}
.btn{ width: 20%; height: 25%;-webkit-appearance:button; border-radius: 5px; background: white;}
.btnFile{ width: 150px; height: 30px;}
.form-control{ width: 40%; height: 20px}
</style>



