<template lang="html">
    <div id="searchPanel"  v-if="display=='search'">
      <div class="div1">
        <button type="button" id="searchNewbtn" class="btn" @click="searchData">查询新纪录</button>
        <span id="userName"></span>
        <span id="msg">msg:</span>
        <ul id="newDetail" class="list-group">
            <li class="bookFindList">{{ message }}</li>
        </ul>
      </div>
      <div class="div2">
        <span id="searchBookbtn">查询书籍</span>
        <input type="text" id="bookName" placeholder="书籍名称"/>
        <button id="searchBtn">查询</button><br />
        <ul id="bookDisplay"><li>{{ book }}</li></ul>
        <ul id="bookafter"></ul>
        <button id="noticeCustomer">通知该用户</button>
        <button id="ignoreCustomer">忽略该用户</button>
      </div>
    </div>
</template>

<script>
import $ from 'jquery'

export default {
  data(){
    return{
      display: 'search',
      message: '快找书啊',
      book: '还没有查询书(=_=)'
    }
  },
  methods:{
    searchData: function() {
        $('#userName').show();
        var jsondata= {
           "json_cmd":"getuserlist",
           "json_limit":"1"
        };

        $.ajax({
            type: "POST",
            url: "http://114.55.92.193:8080/addbook/",
            data:JSON.stringify(jsondata) ,
            contentType: "application/json; charset=utf-8",
            success: function(data){
                var data = data.ret;
                obj=data;
                var user=data[0].userid;
                bookListName="";
                for(var i=0;i<data[0].msglist.length;i++){
                   bookListName +='<li class="list-group-item">'+'<div class="row">'+'<div class="col-lg-8">'
                       +data[0].msglist[i].text+'</div>'+ '<div class="col-lg-2">' + '<div class="input-group">'
                       + '<input type="radio" name='+ "radioFind"+i + ' value="1">'
                       + '<span style="margin-left: 10px">Y'+ '</span>' + '</div>' + '</div>'
                       +'<div class="col-lg-2">' + '<div class="input-group">' + '<input type="radio" name='
                       + "radioFind"+i + ' value="0">' + '<span  style="margin-left: 10px">N'+ '</span>'
                       + '</div>' + '</div>' +'</div>' + '</li>';
                }
                $('#userName').html("用户名："+user);
                $('#newDetail').html(bookListName);
                for(var i=0;i<data[0].msglist.length;i++){
                    var nameStr= 'radioFind'+i;
                    checkRadio[i]= $('input[name='+ nameStr +']').is(':checked');
                    console.log(checkRadio[i]);
                }
            }
        })
    }
  }
}
</script>

<style lang="css">
body,html,ul{margin-top: 10px; padding: 0; list-style-type: none; text-decoration: none; color: black; font-family: "微软雅黑";}
#searchPanel{background: #00CCCC; padding: 1em; display: flex; flex-direction:row; border-radius: 10px; margin-top: 20px; margin: 150px auto;
    width: 90%;}
.div1{flex: 1; -webkit-flex: 1;}
.div2{flex: 1; -webkit-flex: 1; border-left: 1px white solid;color: white; padding-left: 1em;}
#searchNewbtn{background: lightcoral; color: white; display: block;}
#msg{color: white; margin: 20px 0px; border-radius: 5px;}
#userName{ background: white; padding: 10px; border-radius: 5px; display: none; margin-top: 15px;}
#newDetail{ background: white; padding: 10px 10px 30px 10px; border-radius: 5px; width: 80%;}
.list-group-item>input{margin-left: 15px;}
#searchBookbtn{margin-right: 10px;}
#bookName{position: relative; border-radius: 5px; display: inline; width: 20%;}
#searchBtn{position: relative;background: lightcoral; color: white;}
.list-group-item{padding: 8px;}
#bookDisplay{margin-top: 20px; display: block; width: 80%; background: white; padding: 10px 10px 30px 10px; border-radius: 5px; color: black;}
#noticeCustomer,#ignoreCustomer{position: relative; top: 5px; background: lightcoral; margin-bottom: 10px; color: white;}
</style>



